body {
  margin: 0;
  padding: 0;
}

.dev-wrapper {
  // width: 1900px;
  margin: 0 auto;
  width: 100%;
  position: relative;
}
.dev-viewport {
  width: 100%;
  height: 100%;
  position: relative;
}
.dev-slider {
  padding: 0;
  margin: 0;
  position: relative;
  list-style-type: none;

  &:after {
    content: '.-.';
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0;
  }
}
.my-slider {
  img {
    max-width: 100%;
  }

  li {
    display: none;
    &:first-child {
      display: block;
    }
  }
}
.slides,
.slides > li,
.devslider-control-nav,
.devslider-direction-nav {
  margin: 0;
  padding: 0;
  list-style: none;
}



// *********************
// Direction Nav
// *********************

.devslider-direction-nav {
  *height: 0;

  //
  // Global styles
  //
  a {
    text-decoration: none;
    display: block;
    width: 40px;
    height: 40px;
    margin: -20px 0 0;
    position: absolute;
    top: 50%;
    z-index: 10;
    // overflow: hidden;
    // opacity: 0;
    cursor: pointer;
    background: red;
    color: white;

    &:hover {
      background: blue;
    }

    &:before {
      font-family: "@{font-name}";
      font-size: 40px;
      display: inline-block;
    }

    &.devslider-next:before {
      // content: 'go !';
    }
  }

  // Prev
  .devslider-prev {
    left: 0;
  }

  // Next
  .devslider-next {
    right: 0;
    text-align: right;
  }

  // Hover states
  .flexslider:hover & {
    .flex-prev {
      opacity: 0.7;
      left: 10px;
      &:hover {
        opacity: 1;
      }
    }

    .flex-next {
      opacity: 0.7;
      right: 10px;
      &:hover {
        opacity: 1;
      }
    }
  }

  // Disabled
  .devslider-disabled {
    opacity: 0 !important;
    cursor: default;
  }
}




/*------------------------------------*\
    # Pagination Nav
\*------------------------------------*/

.dev-direction-nav {

}

.dev-bullet {
  &.dev-active {
    a {
      color: #ff314f;
    }
  }
}



/*------------------------------------*\
    # Ken Burn
\*------------------------------------*/

.dev-ken-burn-wrapper {
  width: 100%;
  height: 8px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}
.dev-ken-burn-progress {
  width: auto;
  height: 100%;
  background: red;
}
.dev-preloader {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -11px;
  margin-left: -11px;
  background: url(../../assets/images/spinner.gif) no-repeat;
  background-size: cover;
  width: 32px;
  height: 32px;
  z-index: 100;
}



// *********************
// Timers
// *********************

.dev-bar-timer {
  position: absolute;
  width: 0;
  height: 2px;
  background: white;
  border-bottom: 2px solid #555;
  opacity: .55;
  z-index: 4;
  top: 0;
}
.dev-circle-timer {
  width: 16px;
  height: 16px;
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 4;
  opacity: .65;
}
.dev-ct-half {
  background: white;
}
.dev-ct-center {
  background: #444;
}
.dev-ct-left,
.dev-ct-right {
  width: 50%;
  height: 100%;
  overflow: hidden;
}
.dev-ct-left,
.dev-ct-right {
  float: left;
  position: relative;
}
.dev-ct-rotate {
  width: 200%;
  height: 100%;
  position: absolute;
  top: 0;
}
.dev-ct-left .dev-ct-rotate,
.dev-ct-right .dev-ct-hider,
.dev-ct-right .dev-ct-half {
  left: 0;
}
.dev-ct-right .dev-ct-rotate,
.dev-ct-left .dev-ct-hider,
.dev-ct-left .dev-ct-half {
  right: 0;
}
.dev-ct-hider,
.dev-ct-half {
  position: absolute;
  top: 0;
}
.dev-ct-hider {
  width: 50%;
  height: 100%;
  overflow: hidden;
}
.dev-ct-half {
  width: 200%;
  height: 100%;
}
.dev-ct-center {
  width: 50%;
  height: 50%;
  left: 25%;
  top: 25%;
  position: absolute;
}
.dev-ct-half,
.dev-ct-center {
  border-radius: 100px;
}



/*------------------------------------*\
    # TRASH
\*------------------------------------*/

.dev-text {
  position: absolute;
  top: 0;
  left: 0;
  color: #fff;
  z-index: 1;
}
.dev-block {
  position: relative;
}
.pubApi {
  position: fixed;
  bottom: 10px;
  right: 10px;
  width: auto;
}
.my-header {
  height: 50px;
}